<template>
  <div class="ratingselect">
    <div class="rating-type border-1px">
      <span @click="select(2,$event)" class="block positive" :class="{'active':mySelectType===2}">
        {{desc.all}}
        <span class="count">{{ratings.length}}</span>
      </span>
      <span @click="select(0,$event)" class="block positive" :class="{'active':mySelectType===0}">
        {{desc.positive}}
        <span class="count">{{positives.length}}</span>
      </span>
      <span @click="select(1,$event)" class="block negative" :class="{'active':mySelectType===1}">
        {{desc.negative}}
        <span class="count">{{negatives.length}}</span>
      </span>
    </div>
    <div @click="toggleContent($event)" class="switch" :class="{'on':myOnlyContent===true}">
      <i class="icon-check_circle"></i>
      <span class="text">只看有内容的评价</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  const POSITIVE = 0;
  const NEGATIVE = 1;
  const ALL = 2;
  export default {
    props: {
      ratings: {
        type: Array,
        default () {
          return [];
        }
      },
      selectType: {
        type: Number,
        default: ALL
      },
      onlyContent: {
        type: Boolean,
        default: false
      },
      desc: {
        type: Object,
        default () {
          return {
            all: '全部',
            positive: '满意',
            negative: '不满意'
          };
        }
      }
    },
    data () {
      return {
        // 1、创建props属性的副本
        mySelectType: this.selectType,
        myOnlyContent: this.onlyContent,
        result: true
      };
    },
    watch: {
      // 2、监听外部props属性的变更，并同步到data对应的属性中
      selectType (val) {
        this.mySelectType = val;
      },
      onlyContent (val) {
        this.myOnlyContent = val;
      },
      // 3、组件向外部发送事件通知
      mySelectType (val) {
        this.$emit('selectType-change', val);
      },
      myOnlyContent (val) {
        this.$emit('onlyContent-change', val);
      }
    },
    methods: {
      select (type, event) {
        if (!event._constructed) {
          return;
        };
        this.mySelectType = type;
      },
      toggleContent (event) {
        if (!event._constructed) {
          return;
        };
        this.myOnlyContent = !this.myOnlyContent;
      }
    },
    computed: {
      positives () {
        return this.ratings.filter((rating) => {
          return rating.rateType === POSITIVE;
        });
      },
      negatives () {
        return this.ratings.filter((rating) => {
          return rating.rateType === NEGATIVE;
        });
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  /* 注意@符号，css样式一旦写错，后面的样式将不起作用 */
  @import "../../common/stylus/mixin.styl"

  .ratingselect
    .rating-type
      padding 18px 0
      margin 0 18px
      border-1px(rgba(7,17,27,0.1))
      font-size 0
      .block
        display inline-block
        padding 8px 12px
        margin-right 8px
        border-radius 1px
        font-size 12px
        line-height 16px
        color rgb(77,85,93)
        &.active
          color #ffffff
        .count
          font-size 8px
          line-height 16px
          margin-left 2px
        &.positive
          background-color rgba(0,160,220,0.2)
          &.active
            background-color rgb(0,160,220)
        &.negative
          background-color rgba(77,85,93,0.2)
          &.active
            background-color rgb(77,85,93)
    .switch
      padding 12px 18px
      line-height 24px
      color rgb(147,153,159)
      border-bottom 1px solid rgba(7,17,27,0.1)
      font-size 0
      &.on
        .icon-check_circle
          color #00c850
      .icon-check_circle
        display inline-block
        vertical-align top
        margin-right 4px
        font-size 24px
      .text
        font-size 12px
</style>



