<template>
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title">综合评分</div>
          <div class="rank">高于周边商家{{seller.rankRate}}%</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title">服务态度</span>
            <star :size="36" :score="seller.serviceScore"></star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
            <span class="title">商品评分</span>
            <star :size="36" :score="seller.foodScore"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrapper">
            <span class="title">送达时间</span>
            <span class="delivery">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <split></split>
      <ratingselect v-on:selectType-change="onSelectTypeChange" v-on:onlyContent-change="onOnlyContentChange" :select-type="selectType" :only-content="onlyContent" :ratings="ratings"></ratingselect>
      <div class="rating-wrapper">
        <ul>
          <li v-show="needShow(rating.rateType,rating.text)" v-for="rating in ratings" class="rating-item">
            <div class="avatar">
              <img :src="rating.avatar" alt="" width="28px" height="28px">
            </div>
            <div class="content">
              <h1 class="name">{{rating.username}}</h1>
              <div class="star-wrapper">
                <star :size="24" :score="rating.score"></star>
                <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span>
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend.length">
                <span class="icon-thumb_up"></span>
                <span v-for="item in rating.recommend" class="item">{{item}}</span>
                <div class="time">{{rating.rateTime | fomatDate}}</div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import star from '../../components/star/star';
  import split from '../../components/split/split';
  import ratingselect from '../../components/ratingselect/ratingselect';
  import {fomatDate} from '../../common/js/date';
  import BScroll from 'better-scroll';
  const ALL = 2;
  const ERR_OK = 0;

  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data () {
      return {
        ratings: [],
        selectType: ALL,
        onlyContent: true
      };
    },
    filters: {
      fomatDate (time) {
        let date = new Date(time);
        return fomatDate(date, 'yyyy-MM-dd hh:mm');
      }
    },
    components: {
      star,
      split,
      ratingselect
    },
    methods: {
      // 父组件调用子组件的方法，将组件内的数据变更，同步到组件外的数据状态中
      onSelectTypeChange (val) {
        this.selectType = val;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      // 父组件调用子组件的方法，将组件内的数据变更，同步到组件外的数据状态中
      onOnlyContentChange (val) {
        this.onlyContent = val;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      needShow (type, text) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        } else {
          return type === this.selectType;
        }
      }
    },
    created () {
      this.$http.get('/api/ratings').then((response) => {
        response = response.body;
        if (response.errno === ERR_OK) {
          this.ratings = response.data;
          this.$nextTick(() => {
            this.scroll = new BScroll(this.$refs.ratings, {
              click: true
            });
          });
        }
      });
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .ratings
    position absolute
    top 174px
    bottom 0
    width 100%
    left 0
    overflow hidden
    .overview
      display flex
      margin 18px 0
      .overview-left
        text-align center
        padding-left 6px 0
        flex 0 0 137px
        width 137px
        @media only screen and (max-width 320px)
          flex 0 0 120px
          width 120px
        border-right 1px solid rgba(7,17,27,0.1)
        .score
          margin-bottom 8px
          color rgb(255,153,0)
          line-height 28px
          font-size 24px
        .title
          margin-bottom 10px
          font-size 12px
          color rgb(7,17,27)
          line-height 12px
        .rank
          line-height 10px
          font-size 10px
          color rgb(147,153,159)
      .overview-right
        flex 1
        padding-left 24px
        @media only screen and (max-width 320px)
          padding-left 6px
        .score-wrapper
          margin-bottom 6px
          font-size 0
          .title
            display inline-block
            font-size 12px
            color rgb(7,17,27)
            line-height 15px
          .star
            display inline-block
            margin 0 12px
            @media only screen and (max-width 320px)
              margin 0 6px
            vertical-align top
            line-height 15px
          .score
            display inline-block
            vertical-align top
            font-size 12px
            line-height 15px
            color rgb(255,153,0)
        .delivery-wrapper
          font-size 0
          .title
            margin-bottom 8px
            margin-right 12px
            font-size 12px
            color rgb(7,17,27)
            line-height 15px
          .delivery
            font-size 12px
            line-height 14px
            color rgb(147,153,159)
    .rating-wrapper
      padding 0 18px
      .rating-item
        display flex
        padding 18px 0
        border-1px rgba(7,17,27,0.1)
      .avatar
        flex 0 0 28px
        width 28px
        margin-right 12px
        img
          border-radius 50%
      .content
        position relative
        flex 1
        .name
          margin-bottom 4px
          color rgb(7,17,27)
          font-size 10px
          line-height 12px
        .star-wrapper
          margin-bottom 6px
          font-size 0
          .star
            display inline-block
            vertical-align top
            margin-right 6px
            margin-left -2px
          .delivery
            font-size 10px
            font-weight 200
            line-height 12px
            color rgb(147,153,159)
        .text
          margin-bottom 8px
          font-size 12px
          line-height 18px
          color rgb(7,17,27)
        .recommend
          line-height 16px
          .icon-thumb_up, .item
            display inline-block
            font-size 9px
            margin 0 8px 4px 0
          .icon-thumb_up
            color rgb(0,160,220)
          .item
            padding 0 6px
            border 1px solid rgba(7,17,27,0.1)
            border-radius 2px
            background-color #ffffff
            font-size 9px
            color rgb(147,153,159)
            line-height 16px
          .time
            position absolute
            top 0
            right 0
            font-size 10px
            font-weight 200
            line-height 12px
            color rgb(147,153,159)
</style>



